.page {
  height: 100vh;
  overflow-x: scroll;
}

.page-enter {
  opacity: 0;
  transform: scale(1.1);
}

.page-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 300ms, transform 300ms;
}

.page-exit {
  opacity: 1;
  transform: scale(1);
}

.page-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}

body{
  background:#0B0B0B;
}

.appContainer{
  
  border-left: .5rem solid #FFCC51;
}

